var edit_history = [];

function undo_changes() {
    save_state(edit_history.pop());
    console.log(edit_history);
}

function save_history() {
    edit_history.push($("#all-tasks").html());
}

function restore_state() {
    $("#all-tasks").html(localStorage.getItem("data"));
}
restore_state();
function save_state(state = null) {
    $(".text-primary").removeClass("text-primary");
    if (state === null) {
        state = $("#all-tasks").html();
    }
    localStorage.setItem("data", state);
    restore_state();
}

function add_task(elem) {
    save_history();
    $(elem).parent().next().prepend(`<div class="task-item"><div class="task-meta"><div class="task-title" onkeyup="$(this).addClass('text-primary')" onfocusin="save_history();" onfocusout="save_state();" contenteditable="true" style="outline: none">Untitled</div><div class="task-delete-button" onclick="toggle_task_indicator(this);"><div class="task-indicator"></div></div><div class="task-delete-button" onclick="delete_task(this)">delete</div><div class="task-delete-button" onclick="add_task(this)">add</div><div class="task-delete-button" onclick="task_close(this)">expand_more</div></div><div class="task-children"></div></div>`);
    save_state();
}

function toggle_task_indicator(elem) {
    save_history();
    $(elem).children().toggleClass("task-indicator-on");
    save_state();
}

function delete_task(elem) {
    save_history();
    $(elem).parent().parent().remove();
    save_state();
}

function task_close(elem) {
    $(elem).parent().toggleClass("task-meta-closed");
    if ($(elem).parent().hasClass("task-meta-closed"))
        $(elem).html("expand_more");
    else $(elem).html("expand_less");
    $(elem).parent().next().toggle();
}